.banner {
    display: grid;
    grid-template-columns: 3fr 4fr;
}

.banner .box {
    position: relative;
    background-color: #131f37;
    height: 100vh;
}


.banner .content {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 60px;
}

.banner .content .contentBox {
    text-align: center;
}

.banner .content .contentBox img {
    width: 100%;
}

.banner .content .contentBox p {
    font-size: 1.2em;
    color: #fff;
    font-weight: 300;
}

.banner .content .contentBox .btn {
    display: inline-block;
    font-size: 1.1em;
    color: #333;
    background-color: #fff;
    padding: 10px 30px;
    margin-top: 20px;
    border-radius: 30px;
    letter-spacing: 1px;
    text-decoration: none;
    font-weight: 500;
    transition: 0.2s;
}

.banner .content .contentBox .btn:hover {
    letter-spacing: 2px;
}

.socials {
    position: absolute;
    bottom: 30px;
    right: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25px;
}

.socials h3 {
    font-weight: 600;
    color: rgb(128, 128, 128);
    letter-spacing: 1px;
}

.socials li {
    list-style: none;
}

.socials li img {
    max-width: 30px;
}

@media (max-width: 991px) {
    .banner {
        grid-template-columns: 1fr;
    }

    .banner .content {
        padding: 35px;
        height: 70vh;
    }

    .socials {
        right: 50px;
    }
}