.banner .box {
    position: relative;
    background-color: #131f37;
    height: 100vh;
}

.banner .images {
    background-color: #fff;
    display: flex;
    flex-wrap: wrap;
}

.banner .images .imgBox {
    position: relative;
    width: 50%;
    height: 50vh;
}

.banner .images .imgBox:nth-child(2) {
    width: 50%;
}

.banner .images .imgBox:nth-child(3) {
    width: 100%;
}

.banner .images .imgBox img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}