header {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    padding: 20px 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header .logo img {
    max-width: 200px;
}

header ul {
    display: flex;
    gap: 40px;
}

header ul li {
    list-style-type: none;
}

header ul li a {
    text-decoration: none;
    font-weight: bold;
    color: rgb(128, 128, 128);
    letter-spacing: 1px;
}

header ul li a:hover {
    color: #fff;
}


@media (max-width: 991px) {
    header {
        padding: 20px 40px;
    }
}